<template>
	<div class="saleedi-user-info">
		<van-cell-group class="urser-info-form fx-edi-block">
			<van-field v-model="urserName" label="联系人" readonly />
			<van-field
				v-model="telephone"
				label="联系电话"
				right-icon="edit"
				@click-right-icon="editphoneClick"
				readonly />
			<van-field
				v-model="email"
				label="邮箱"
				:right-icon="showMailEditIcon"
				placeholder="请输入邮箱地址,非必填"/>
		</van-cell-group>
		<div>
			<van-button class="info-btn" type="primary" round @click="saveHandler">保存</van-button>
			<van-button class="info-btn" type="warning" round @click="unBindClick">解除绑定</van-button>
		</div>
		<van-actionsheet
			v-model="unBindMenuShow"
			:actions="actions"
			cancel-text="取消"
			@select="onSelect"
		/>
	</div>
</template>

<script>
export default {
	name: 'userInfo',
	data () {
		return {
			urserName: '',
			telephone: '',
			email: '',
			unBindMenuShow: false,
			actions: [
				{
					name: '企业一'
				},
				{
					name: '企业二'
				},
				{
					name: '企业三'
				}
			]
		}
	},
	created () {
		this.getUser()
	},
	computed: {
		showMailEditIcon () {
			if (this.mail !== '' && this.mail !== undefined) {
				return ''
			}
			return 'edit'
		}
	},
	methods: {
		unBindClick () {
			this.unBindMenuShow = true
		},
		onSelect (item) {
			this.$dialog.confirm({
				message: `是否解绑${item.name}`
			}).then(() => {
				this.unBindMenuShow = false
				this.$toast('解绑成功')
			}).catch(() => {
			})
		},
		editphoneClick () {
			return false
			// this.$router.push('/userInfoEdit')
		},
		getUser () {
			this.$fxHttp.requestPost({
				url: '/getUser'
			}).then(res => {
				this.urserName = res.userName
				this.telephone = res.telephone
				this.email = res.email
			})
		},
		saveHandler () {
			const params = {
				id: this.userId,
				email: this.email
			}
			this.$fxHttp.requestPost({
				url: '/updateUser',
				data: params
			}).then(res => {
				this.$toast.success('保存成功')
			})
		}
	}
}
</script>
<style lang="stylus" scoped>
@import '../../assets/stylus/varsty.styl'
.saleedi-user-info {
	height:100%
	background:#fff
	.urser-info-form {
		width:320px
		margin:10px auto
		text-align:left
	}
	.info-btn {
		margin-top:20px
		width:320px
	}
}
</style>
